
import moment from 'moment'
import util,{_} from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import MyCanlendar from 'c%/mycanlendar/MyCanlendar'
import Tag from 'c%/Tag.vue'
import ArrangeChangePopup from './schedule/arrange-change-popup.vue'
import Card from 'vux/src/components/card/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default{
mixins: [common,globals],
components:{
Tag,
Card,
Divider,
Flexbox,
LoadMore,
FlexboxItem,
MyCanlendar,
ArrangeChangePopup
},
data () {
return {
data: [],
events: [],
length: 5,
current_day: this.$route.query.datetime || moment(new Date()).format("YYYY-MM-DD")
}
},
watch: {
'current_day': function (val) {
this.data_uri = this.$data_uri
this.init_data()
}
},
mounted () {
this.data_uri = this.$data_uri
this.pageIndex = 0
this.getEvents()
this.loadMore()

},
methods: {
arrangeDetail (item) {
if(item.is_cancel==1){
return
}
this.$router.push({path:'./schedule/'+item.ca_id})
},
showChangePopup (row) {
this.$refs.change
.set('info',row)
.show('')
},
getAttendanceName (arrange) {
let	mapTitle = {1:'一对一',2:'一对多'},
result = ''
if(arrange.lesson_type===0){
result = arrange.one_class!=null?arrange.one_class.class_name:arrange.name
}else{
result = mapTitle[arrange.lesson_type]
}

return result
},
prepareDetail (item) {
let cp_id = item.course_prepare.cp_id
this.$router.push({path:'./prepare/'+cp_id,params: {id:cp_id}})
},
showBtn (row) {
let nowday = moment(new Date()).format('YYYYMMDD')
let nowhour = moment(new Date()).format('HHmm')
let sub = row.int_day - nowday
if(sub<4&&sub>-1&&!row.attendance&&row.attendance_status===0){
if(sub==0){
return parseInt(row.int_start_hour)>parseInt(nowhour)
}
return true
}
return false
},
init_data () {
this.loading = true
this.dr().getData(this.$data_uri,false)
.then(data=>{
this.data = data.list
setTimeout(()=>{
this.loading = false
},500)
})
},
refreshData () {
this.loading = true
this.dr().refreshData(this.$data_uri)
.then(data=>{
this.data = data.list
setTimeout(()=>{
this.loading = false
},500)
})
},
getEvents () {
this.$rest(this.$event_uri)
.get()
.success(data=>{
if(data.list.length>0){
data.list.forEach(d=>{
this.events.push(this.$filter('int_date')(d))
})
}
})
}
},
computed: {
currentDay () {
let result = moment(this.current_day).format('YYYYMMDD')
return result
},
allow_mobile_change () {
let result = true

if(this.$store.state.gvars.configs.params&&this.$store.state.gvars.configs.params.course_arrange){
result = this.$store.state.gvars.configs.params.course_arrange.allow_mobile_change
}

return result
},
$data_uri () {
return util.sprintf('course_arranges?bid=-1&with=one_class,course_prepare&teach_eid=%s&int_day=%s',this.eid$,this.currentDay)
},
$event_uri () {
return util.sprintf('course_arranges/get_course_day?bid=-1&teach_eid=%s&pagesize=1000',this.eid$)
}
}
}



