
import vue from 'vue'
import moment from 'moment'
import util from '@/libs/util'
import modal from '@/libs/modal.mixin'
import common from '@/libs/common.mixin'

import Tag from 'c%/Tag.vue'
import PopupPicker from 'c%/PopupPicker.vue'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import Popup from 'vux/src/components/popup/index.vue'


vue.use(DatetimePlugin)

const emptyObject = {
bid: 0,
is_cancel: 0,
int_day: '',
int_start_hour: '',
int_end_hour: '',
reason: ''
}

export default{
mixins: [ common, modal ],
components: {
Tag,
Popup,
PopupPicker
},
data () {
return {
info: util.copy(emptyObject),
schedules: [],
current_tsid: '',
create_sa: 1
}
},
watch: {
'info.int_day': function (val) {
if(val){
this.dateChange(this.$filter('int_date')(val))
}
}
},
computed: {
style () {
return {
top: '100px',
width: 'calc(100% - 40px)',
left: '20px',
overflow: 'hidden'
}
},
schedulesList () {
let list = this.schedules,
result = []
if(list && list.length){
list.forEach(l=>{
let obj = {
key: l.tsid+' ~ '+l.int_start_hour + ' ~ ' + l.int_end_hour,
value: l.int_start_hour + ' ~ ' + l.int_end_hour
}
result.push(obj)
})
}

return result
},
season () {
let bid = this.info.bid,
season = this.get_season_by_date(bid,new Date(this.info.int_day))

if(this.dicts('season').findIndex(s=>s.name=='season') > -1){
return season
}
return 'A'
}
},
methods: {
hourChange (val) {
this.info.int_start_hour = util.format_int_hour(val.split(' ~ ')[1])
this.info.int_end_hour = util.format_int_hour(val.split(' ~ ')[2])
},
dateChange (val) {
let int_day = this.$filter('int_date')(this.info.int_day),
bid = this.info.bid,
week_day = util.get_week_day(new Date(val))


this.schedules = this.get_time_sections(bid,week_day,this.season)

if(this.schedules.length==0){
this.toast(`${int_day}没有设置上课时段,请选择其他日期!`,'warn')
}

},
changeType (val) {
this.$set(this.info,'is_cancel',val)
},
showDate () {
let $this = this
this.$vux.datetime.show({
cancelText: '取消',
confirmText: '确定',
format: 'YYYY-MM-DD',
startDate: moment(new Date()).format('YYYY-MM-DD'),
value: moment(new Date()).format('YYYY-MM-DD'),
onConfirm (val) {
$this.info.int_day = util.format_int_day(val)
},
onShow () {
console.log('plugin show')
},
onHide () {
console.log('plugin hide')
}
})
},
showHour () {
this.$refs.hour.popup = true
},
ok () {
this.showLoading()

if(this.info.is_cancel==1){
this.cancelArrange()
}else{
this.changeDate()
}

},
changeDate () {
this.$rest(`course_arranges/${this.info.ca_id}`)
.put(this.info)
.success(data=>{
this.hideLoading()
this.toast('调课成功','success')
this.$emit('on-success')
this.close()
}).error(response=>{
this.hideLoading()
this.toast(response.body.message||'调课失败~','warn')
})
},
cancelArrange () {
let params = {
ca_id: this.info.ca_id,
create_sa: this.create_sa,
reason: this.info.reason
}

this.$rest(`course_arranges/${this.info.ca_id}/docancel_course`)
.post(params)
.success(data=>{
this.hideLoading()
this.toast('排课取消成功','success')
this.$emit('on-success')
this.close()
}).error(response=>{
this.hideLoading()
this.toast(response.body.message||'排课取消失败~','warn')
})
}
}
}



