import format from './format'
import { getDays, zero } from './util'
import props from './props'
import moment from 'moment'

export default{
  name: 'my-calendar',
  props: props(),
  data () {
	  return {
	    year: 0,
		month: 0,
		days: [],
		today: format(new Date(), 'YYYY-MM-DD'),
		months: ['01', '02', '03', '04', '05', '06', '07', '08', '09', '10', '11', '12'],
		currentValue: '',
		currentRow: 0,
		sub_y: 0,
		calendar_type: this.type||'week',
		is_change: false,
	  }
  },
  created () {
    this.currentValue = this.value
  },
  mounted () {
    this.currentValue = this.convertDate(this.currentValue)
    this.render(this.renderMonth[0], this.renderMonth[1] - 1)    
    setTimeout(()=>{
    	let current = document.getElementsByClassName('is-today')
    	this.currentRow = parseInt(current[0].parentNode.getAttribute('data-columns'))	
    	this.currentValue = this.value || current[0].getAttribute('data-date')
    },100)
  },
  computed: {
  	title () {
  		return moment(this.currentValue).format('YYYY年MM月DD')
  	},
    _weeksList () {
      if (!this.weeksList || !this.weeksList.length) {
        return ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa']
      } else {
        return this.weeksList
      }
    },
    _replaceTextList () {
      const rs = {}
      for (let i in this.replaceTextList) {
        rs[this.convertDate(i)] = this.replaceTextList[i]
      }
      return rs
    }
  },
  watch: {
    value (val) {
        this.currentValue = val
    },
    currentValue (val) {
      this.currentValue = this.convertDate(val)
      if (this.renderOnValueChange) {
        this.render(null, null, val)
      } else {
        this.render(this.year, this.month, this.currentValue)
      }
      
      setTimeout(()=>{
    	let current = document.getElementsByClassName('current')
    	this.currentRow = parseInt(current[0].parentNode.getAttribute('data-columns'))
      },100)
	  
	  if(this.is_change){
	  	this.$emit('input', val)
      	this.$emit('on-change', val)	
	  }
      
    },
    renderFunction () {
      this.render(this.year, this.month, this.currentValue)
    },
    renderMonth (val) {
      if (val && val.length === 2) {
        this.render(val[0], val[1] - 1)
      }
    },
    returnSixRows (val) {
      this.render(this.year, this.month, this.currentValue)
    },
    startDate (val) {
      this.render(this.year, this.month, this.currentValue)
    },
    endDate (val) {
      this.render(this.year, this.month, this.currentValue)
    },
    disablePast () {
      this.render(this.year, this.month, this.currentValue)
    },
    disableFuture () {
      this.render(this.year, this.month, this.currentValue)
    }
  },
  methods: {
  	changeCalendarType () {
		this.calendar_type = (this.calendar_type==='month'?'week':'month')		
  	},
    replaceText (day, formatDay) {
      let text = this._replaceTextList[formatDay]
      if (!text && typeof text === 'undefined') {
        return day
      } else {
        return text
      }
    },
    convertDate (date) {
      return date === 'TODAY' ? this.today : date
    },
    buildClass (index, child, isCurrent, datetime) {  
      const className = {
        current: child.current || isCurrent,
        'is-disabled': child.disabled,
        'is-today': child.isToday        
      }
      className[`is-week-${index}`] = true
      if(this.eventsList.length > 0 && this.eventsList.includes(datetime)){
      	className[`is-event`] = true
      }
      return className
    },
    render (year, month) {
      let data = getDays({
        year: year,
        month: month,
        value: this.currentValue,
        rangeBegin: this.convertDate(this.startDate),
        rangeEnd: this.convertDate(this.endDate),
        returnSixRows: this.returnSixRows,
        disablePast: this.disablePast,
        disableFuture: this.disableFuture
      })
      this.days = data.days
      this.year = data.year
      this.month = data.month
    },
    formatDate: (year, month, child) => {
      return [year, zero(child.month + 1), zero(child.day)].join('-')
    },
    prevWeek () {
    	if(this.currentValue){
    		this.is_change = false
    		this.currentValue = moment(this.currentValue).subtract(7,'days').format('YYYY-MM-DD')	
    	}    	 
    },
    nextWeek () {
    	if(this.currentValue){
    		this.is_change = false
    		this.currentValue = moment(this.currentValue).add(7,'days').format('YYYY-MM-DD')
    	}
    },
    prev () {
      if (this.month === 0) {
        this.month = 11
        this.year = this.year - 1
      } else {
        this.month = this.month - 1
      }
      this.currentValue = moment(this.currentValue).subtract(1,'months').format('YYYY-MM-DD')
      this.render(this.year, this.month)
    },
    next () {
      if (this.month === 11) {
        this.month = 0
        this.year = this.year + 1
      } else {
        this.month = this.month + 1
      }
      this.currentValue = moment(this.currentValue).add(1,'months').format('YYYY-MM-DD')
      this.render(this.year, this.month)
    },
    go (year, month) {
      this.render(year, month)
    },
    select (k1, k2, data) {
	  this.is_change = true
      if (data.isLastMonth && !this.showLastMonth) {
        return
      }
      if (data.isNextMonth && !this.showNextMonth) {
        return
      }
      if (!data.isBetween) {
        return
      }
      if (!data.isLastMonth && !data.isNextMonth) {      	
        this.days[k1][k2].current = true
        this.currentValue = [this.year, zero(this.month + 1), zero(this.days[k1][k2].day)].join('-')
      } else {
        this.currentValue = [data.year, zero(data.month + 1), zero(data.day)].join('-')        
      }      
      
    },
    showChild (year, month, child) {
      if (this.replaceText(child.day, this.formatDate(year, month, child))) {
        return (!child.isLastMonth && !child.isNextMonth) || (child.isLastMonth && this.showLastMonth) || (child.isNextMonth && this.showNextMonth)
      } else {
        return false
      }
    }
  }
}



